import {
  PRODUTOS_CATEGORIAS_GET,
  PRODUTOS_GET,
  PRODUTOS_ERROR
} from "../actions/actionTypes";

const INITIAL_STATE = {
  categorias: [],
  camarotes: [],
  blocos: [],
  passaportes: [],
  error: false
};

export default function productsReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case PRODUTOS_CATEGORIAS_GET:
      return {
        ...state,
        categorias: action.data
      };
    case PRODUTOS_GET:
      return {
        ...state,
        camarotes: action.categoria_id === 16 ? action.data : state.camarotes,
        blocos: action.categoria_id === 17 ? action.data : state.blocos,
        passaportes:
          action.categoria_id === 19 ? action.data : state.passaportes
      };
    case PRODUTOS_ERROR:
      return {
        ...state,
        error: action.error
      };
    default:
      return state;
  }
}
