import { WOO_API } from "../woocommerce";
import * as _ from "lodash";
import {
  PRODUTOS_CATEGORIAS_GET,
  PRODUTOS_GET,
  PRODUTOS_ERROR
} from "./actionTypes";

export function produtosCategoriasGetAction(products) {
  return { type: PRODUTOS_CATEGORIAS_GET, data: products };
}

export function produtosGetAction(products, categoria_id) {
  return { type: PRODUTOS_GET, data: products, categoria_id };
}

export function produtosErrorAction(error) {
  return { type: PRODUTOS_ERROR, error };
}

export function produtosGet(dispatch, categoria_id, paramargs = []) {
  const internalargs = {
    status: "publish",
    category: categoria_id,
    orderby: "menu_order",
    order: "asc",
    per_page: "100"
  };
  WOO_API.get("products/", _.merge(internalargs, paramargs))
    .then(response => {
      dispatch(produtosGetAction(response.data, categoria_id));
    })
    .catch(error => {
      dispatch(produtosErrorAction(error.response));
    });
}

export function produtosCategoriasGet(dispatch) {
  WOO_API.get("products/categories/")
    .then(response => {
      dispatch(produtosCategoriasGetAction(response.data));
    })
    .catch(error => {
      dispatch(produtosErrorAction(error.response));
    });
}
